.footer {
  height: 359px;
  position: sticky;
  bottom: 0;
  z-index: -1;
  background-image: url("../images/footer.svg");
  background-size: cover;
  background-position: center;

  &__logo {
    position: absolute;
    margin: auto;
    bottom: 40px;
    left: 50%;
    transform: translateX(-50%);
  }

  &__logo_img {
    height: 76px;
  }
}
