.section {
  position: relative;
  padding-top: 104px;
  padding-bottom: 0;
  padding-left: 120px;
  padding-right: 120px;

}
@media screen and (max-width: 999px) {
  .section {
  padding-left: 80px;
  padding-right: 80px;
  }
}
@media screen and (max-width: 750px) {
  .section {
  padding-left: 40px;
  padding-right: 40px;
  }
}

@media screen and (max-width: 500px) {
  .section {
  padding-left: 20px;
  padding-right: 21px;
  }
}
